@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}
#hoverLogo:hover {
    content: url(/static/media/logo_coral_drop_shadow.0bedd16d.png);
}
.nav {
  overflow: hidden;
  background-color: #00adb000;
}

.nav-link {
  /* float: left; */
  /* color: #f2f2f2; */
  /* background-color: #00acb0; */
  /* text-align: center; */
  /* padding: 24px 10px; */
  text-decoration: none;
  /* font-size: 15px; */
  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #00818300;
}

.nav-right {
  float: right;
}

