/* Font: Source Sans Pro  */

body {
    font-family: 'Source Sans Pro', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    background-color: rgba(245, 245, 245, 0.539);
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 10px;
}

.form {
    background-color: #dbd5d5;
    width: 30%;
    height: 500px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 10px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #ff4545;
  border-radius: 4px;
  color: black;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #f03d3d;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

.DemoBackground{
    background: #d8d8d8; 
}

#search::-webkit-input-placeholder {
        color: #ffffff;
        opacity: 0.50;
    }

#search::-ms-input-placeholder {
        color: #ffffff;
        opacity: 0.50;
    }

#search::placeholder {
        color: #ffffff;
        opacity: 0.50;
    }

